
'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow, TableCaption } from '@/components/ui/table';
import { ScrollArea } from '@/components/ui/scroll-area';
import { useToast } from '@/hooks/use-toast';
import { PlusCircle, Trash2, Edit, UserPlus, Users, MapPin as LocationIcon, Clock as TimeIcon, AlertTriangle, UserCheck, Phone, Lock } from 'lucide-react'; // Added Lock
import type { Employee, LoggedInUser } from '@/lib/types';
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogDescription,
    DialogFooter,
    DialogTrigger,
    DialogClose,
} from "@/components/ui/dialog";
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';


// Zod schema for employee data validation
const employeeSchema = z.object({
  employeeId: z.string().min(1, { message: "Employee ID is required." }),
  name: z.string().min(2, { message: "Name must be at least 2 characters." }),
  phoneNumber: z.string() // Keep phone number for identification, but make it read-only in edit form
    .min(10, { message: "Phone number must be 10 digits." })
    .max(10, { message: "Phone number must be 10 digits." })
    .regex(/^\d{10}$/, { message: "Enter a valid 10-digit phone number without country code." }),
  pin: z.string()
    .length(4, { message: "PIN must be exactly 4 digits." })
    .regex(/^\d{4}$/, { message: "PIN must contain only numbers." }),
  workingLocation: z.string().optional(),
  shiftTiming: z.string().optional(),
});

type EmployeeFormData = z.infer<typeof employeeSchema>;

export default function EmployeeManagementPage() {
  const router = useRouter();
  const { toast } = useToast();
  const [employees, setEmployees] = useState<Employee[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isAdmin, setIsAdmin] = useState(false);
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false);
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false); // State for edit dialog
  const [selectedEmployee, setSelectedEmployee] = useState<Employee | null>(null); // Employee being edited
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Form hook for adding/editing employees
   const form = useForm<EmployeeFormData>({
    resolver: zodResolver(employeeSchema),
    defaultValues: {
      employeeId: '',
      name: '',
      phoneNumber: '',
      pin: '',
      workingLocation: '',
      shiftTiming: '',
    },
  });

  // Watch changes for debugging or conditional logic if needed
  // const watchedFields = form.watch();
  // useEffect(() => { console.log("Form values:", watchedFields); }, [watchedFields]);

  // Check admin status and load employees
  useEffect(() => {
    const loggedInUserString = localStorage.getItem('loggedInUser');
    if (!loggedInUserString) {
      toast({ variant: 'destructive', title: 'Not Logged In', description: 'Please log in as admin.' });
      router.push('/login');
      return;
    }
    try {
      const user: LoggedInUser = JSON.parse(loggedInUserString);
      if (user.type !== 'admin') {
        toast({ variant: 'destructive', title: 'Access Denied', description: 'Only admins can manage employees.' });
        router.push('/');
        return;
      }
      setIsAdmin(true);
    } catch (e) {
      console.error("Failed to parse user data", e);
      toast({ variant: 'destructive', title: 'Login Error', description: 'Please log in again.' });
      localStorage.removeItem('loggedInUser');
      router.push('/login');
      return;
    }

    // Load employees from localStorage
    const storedEmployees = localStorage.getItem('employees');
    if (storedEmployees) {
      try {
        setEmployees(JSON.parse(storedEmployees));
      } catch (e) {
        console.error("Failed to parse employees:", e);
        toast({ variant: 'destructive', title: 'Data Error', description: 'Could not load employee data.' });
        setEmployees([]);
      }
    }
    setIsLoading(false);
  }, [router, toast]);

  // --- CRUD Operations ---

  // Add Employee
  const handleAddEmployee = (data: EmployeeFormData) => {
    setIsSubmitting(true);
    const newEmployee: Employee = {
      ...data,
      id: data.phoneNumber, // Use phone number as the internal ID for data linking
    };

    // Check if phone number OR Employee ID already exists
    if (employees.some(emp => emp.phoneNumber === newEmployee.phoneNumber)) {
        form.setError("phoneNumber", { type: "manual", message: "Phone number already exists." });
        toast({ variant: 'destructive', title: 'Error', description: 'This phone number is already registered.' });
        setIsSubmitting(false);
        return;
    }
    if (employees.some(emp => emp.employeeId === newEmployee.employeeId)) {
        form.setError("employeeId", { type: "manual", message: "Employee ID already exists." });
        toast({ variant: 'destructive', title: 'Error', description: 'This Employee ID is already registered.' });
        setIsSubmitting(false);
        return;
    }

    const updatedEmployees = [...employees, newEmployee];
    setEmployees(updatedEmployees);
    localStorage.setItem('employees', JSON.stringify(updatedEmployees));
    toast({ title: 'Success', description: `Employee ${newEmployee.name} (ID: ${newEmployee.employeeId}) added.` });
    form.reset(); // Reset form fields
    setIsAddDialogOpen(false); // Close dialog
    setIsSubmitting(false);
  };

  // Open Edit Dialog
  const handleOpenEditDialog = (employee: Employee) => {
    setSelectedEmployee(employee);
    // Populate the form with the selected employee's data
    form.reset({
        employeeId: employee.employeeId,
        name: employee.name,
        phoneNumber: employee.phoneNumber, // Keep original phone number
        pin: employee.pin, // Or set to '' if PIN shouldn't be shown directly
        workingLocation: employee.workingLocation || '',
        shiftTiming: employee.shiftTiming || '',
    });
    setIsEditDialogOpen(true);
  };

  // Handle Edit Employee Submission
  const handleEditEmployee = (data: EmployeeFormData) => {
      if (!selectedEmployee) return;

      setIsSubmitting(true);

      // Check if Employee ID is being changed and if the new one already exists (excluding the current employee)
      if (data.employeeId !== selectedEmployee.employeeId && employees.some(emp => emp.employeeId === data.employeeId && emp.phoneNumber !== selectedEmployee.phoneNumber)) {
          form.setError("employeeId", { type: "manual", message: "Employee ID already exists." });
          toast({ variant: 'destructive', title: 'Error', description: 'This Employee ID is already used by another employee.' });
          setIsSubmitting(false);
          return;
      }

      const updatedEmployees = employees.map(emp =>
          emp.phoneNumber === selectedEmployee.phoneNumber // Find employee by phone number (ID)
              ? { ...emp, ...data } // Update with new data
              : emp
      );

      setEmployees(updatedEmployees);
      localStorage.setItem('employees', JSON.stringify(updatedEmployees));
      toast({ title: 'Success', description: `Employee ${data.name} (ID: ${data.employeeId}) updated.` });
      setIsEditDialogOpen(false);
      setSelectedEmployee(null);
      form.reset(); // Reset form
      setIsSubmitting(false);
  };


  // Delete Employee
  const handleDeleteEmployee = (phoneNumber: string) => {
    // Find employee to get name for confirmation
    const employeeToDelete = employees.find(emp => emp.phoneNumber === phoneNumber);
    const confirmMessage = employeeToDelete
        ? `Are you sure you want to delete employee ${employeeToDelete.name} (ID: ${employeeToDelete.employeeId})? This action cannot be undone.`
        : 'Are you sure you want to delete this employee? This action cannot be undone.';

    if (window.confirm(confirmMessage)) {
        const updatedEmployees = employees.filter(emp => emp.phoneNumber !== phoneNumber);
        setEmployees(updatedEmployees);
        localStorage.setItem('employees', JSON.stringify(updatedEmployees));
        toast({ title: 'Success', description: 'Employee deleted.' });
        // Consider removing attendance history (implement if needed)
        // removeAttendanceHistoryForUser(phoneNumber);
    }
  };

   // Function to potentially remove attendance history (optional)
  // const removeAttendanceHistoryForUser = (userIdToRemove: string) => { ... }

  // --- Render Logic ---
  if (!isAdmin && !isLoading) {
      return (
          <div className="flex justify-center items-center h-full">
             <Alert variant="destructive" className="max-w-md">
                  <AlertTriangle className="h-4 w-4"/>
                  <AlertTitle>Access Denied</AlertTitle>
                  <AlertDescription>
                     You must be logged in as an administrator to manage employees.
                  </AlertDescription>
             </Alert>
          </div>
      );
  }

  if (isLoading) {
    return <div className="text-center p-10">Loading Employee Data...</div>;
  }

  const renderEmployeeFormFields = (isEditMode = false) => (
     <div className="grid gap-4 py-4">
         <FormField
            control={form.control}
            name="employeeId"
            render={({ field }) => (
                <FormItem>
                <FormLabel className="flex items-center gap-1"><UserCheck className="h-4 w-4"/>Employee ID</FormLabel>
                <FormControl>
                    {/* Make Employee ID read-only in edit mode for simplicity */}
                    <Input placeholder="Unique Employee ID (e.g., E001)" {...field} readOnly={isEditMode} className={isEditMode ? "bg-muted cursor-not-allowed" : ""} />
                </FormControl>
                {isEditMode && <p className="text-xs text-muted-foreground">Employee ID cannot be changed after creation.</p>}
                <FormMessage />
                </FormItem>
            )}
        />
        <FormField
            control={form.control}
            name="name"
            render={({ field }) => (
                <FormItem>
                <FormLabel>Name</FormLabel>
                <FormControl>
                    <Input placeholder="Full Name" {...field} />
                </FormControl>
                <FormMessage />
                </FormItem>
            )}
        />
        <FormField
            control={form.control}
            name="phoneNumber"
            render={({ field }) => (
                <FormItem>
                <FormLabel className="flex items-center gap-1"><Phone className="h-4 w-4"/>Phone Number (Login ID)</FormLabel>
                <FormControl>
                    <Input
                      type="tel"
                      placeholder="10-digit number (e.g., 1234567890)"
                      {...field}
                      readOnly={isEditMode} // Make read-only in edit mode
                      className={isEditMode ? "bg-muted cursor-not-allowed" : ""}
                    />
                </FormControl>
                 {isEditMode && <p className="text-xs text-muted-foreground">Phone number cannot be changed after creation.</p>}
                <FormMessage />
                </FormItem>
            )}
         />
         <FormField
            control={form.control}
            name="pin"
            render={({ field }) => (
                <FormItem>
                <FormLabel><Lock className="inline h-4 w-4 mr-1"/>4-Digit PIN (Login)</FormLabel>
                <FormControl>
                    {/* Use password type for security, but allow editing */}
                    <Input type="password" maxLength={4} placeholder={isEditMode ? "Enter new 4-digit PIN" : "****"} {...field} />
                </FormControl>
                 {isEditMode && <p className="text-xs text-muted-foreground">Enter a new PIN if you want to change it.</p>}
                <FormMessage />
                </FormItem>
            )}
        />
        <FormField
            control={form.control}
            name="workingLocation"
            render={({ field }) => (
                <FormItem>
                <FormLabel className="flex items-center gap-1"><LocationIcon className="h-4 w-4"/>Working Location (Optional)</FormLabel>
                <FormControl>
                    <Input placeholder="e.g., Site A, Main Office" {...field} />
                </FormControl>
                <FormMessage />
                </FormItem>
            )}
        />
         <FormField
            control={form.control}
            name="shiftTiming"
            render={({ field }) => (
                <FormItem>
                <FormLabel className="flex items-center gap-1"><TimeIcon className="h-4 w-4"/>Shift Timing (Optional)</FormLabel>
                <FormControl>
                    <Input placeholder="e.g., 9 AM - 5 PM, Mon-Fri" {...field} />
                </FormControl>
                <FormMessage />
                </FormItem>
            )}
        />
    </div>
  );

  return (
    <Card className="shadow-lg">
      <CardHeader>
        <div className="flex justify-between items-center">
          <CardTitle className="text-2xl font-bold text-primary flex items-center gap-2">
            <Users className="h-6 w-6" /> Employee Management
          </CardTitle>
          {/* Add Employee Dialog */}
           <Dialog open={isAddDialogOpen} onOpenChange={(open) => { setIsAddDialogOpen(open); if (!open) form.reset(); }}>
            <DialogTrigger asChild>
              <Button size="sm">
                <PlusCircle className="mr-2 h-4 w-4" /> Add Employee
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
                <Form {...form}>
                   {/* IMPORTANT: Use a different handler for add vs edit */}
                  <form onSubmit={form.handleSubmit(handleAddEmployee)}>
                     <DialogHeader>
                        <DialogTitle className="flex items-center gap-2"><UserPlus/>Add New Employee</DialogTitle>
                        <DialogDescription>
                            Enter the details for the new employee. They will use their 10-digit phone number and PIN to log in.
                        </DialogDescription>
                     </DialogHeader>
                     {renderEmployeeFormFields(false)} {/* Render form fields for adding */}
                     <DialogFooter>
                        <DialogClose asChild>
                            <Button type="button" variant="outline">Cancel</Button>
                        </DialogClose>
                        <Button type="submit" disabled={isSubmitting}>
                            {isSubmitting ? 'Adding...' : 'Add Employee'}
                        </Button>
                    </DialogFooter>
                  </form>
                </Form>
            </DialogContent>
          </Dialog>
        </div>
        <CardDescription>
          Add, view, edit, and manage employee details and login credentials.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <ScrollArea className="h-[60vh] rounded-md border">
          <Table>
            <TableCaption>
              {employees.length === 0 ? 'No employees added yet.' : 'List of registered employees.'}
            </TableCaption>
            <TableHeader className="sticky top-0 bg-secondary">
              <TableRow>
                <TableHead>Employee ID</TableHead>
                <TableHead>Name</TableHead>
                <TableHead>Phone Number (Login ID)</TableHead>
                {/* <TableHead>PIN</TableHead> */} {/* Maybe hide PIN */}
                <TableHead><LocationIcon className="inline-block mr-1 h-4 w-4"/>Location</TableHead>
                <TableHead><TimeIcon className="inline-block mr-1 h-4 w-4"/>Shift</TableHead>
                <TableHead className="text-right">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {employees.map((employee) => (
                <TableRow key={employee.id}>
                  <TableCell className="font-medium">{employee.employeeId}</TableCell>
                  <TableCell>{employee.name}</TableCell>
                  <TableCell>{employee.phoneNumber}</TableCell>
                  {/* <TableCell>****</TableCell> */} {/* Display **** for PIN */}
                  <TableCell>{employee.workingLocation || 'N/A'}</TableCell>
                  <TableCell>{employee.shiftTiming || 'N/A'}</TableCell>
                  <TableCell className="text-right space-x-1">
                    {/* Edit Button */}
                     <Button
                      variant="ghost"
                      size="icon"
                      className="text-primary hover:text-primary/80"
                      onClick={() => handleOpenEditDialog(employee)}
                      title={`Edit ${employee.name}`}
                    >
                      <Edit className="h-4 w-4" />
                      <span className="sr-only">Edit Employee</span>
                    </Button>

                    {/* Delete Button */}
                    <Button
                      variant="ghost"
                      size="icon"
                      className="text-destructive hover:text-destructive"
                      onClick={() => handleDeleteEmployee(employee.phoneNumber)}
                      title={`Delete ${employee.name}`}
                    >
                      <Trash2 className="h-4 w-4" />
                       <span className="sr-only">Delete Employee</span>
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </ScrollArea>
      </CardContent>

        {/* Edit Employee Dialog */}
       <Dialog open={isEditDialogOpen} onOpenChange={(open) => { setIsEditDialogOpen(open); if (!open) { setSelectedEmployee(null); form.reset(); } }}>
         <DialogContent className="sm:max-w-[425px]">
           <Form {...form}>
              {/* IMPORTANT: Use a different handler for edit */}
             <form onSubmit={form.handleSubmit(handleEditEmployee)}>
               <DialogHeader>
                 <DialogTitle className="flex items-center gap-2"><Edit className="h-5 w-5"/>Edit Employee Details</DialogTitle>
                 <DialogDescription>
                   Update the employee's information. Phone number and Employee ID cannot be changed.
                 </DialogDescription>
               </DialogHeader>
               {renderEmployeeFormFields(true)} {/* Render form fields for editing */}
               <DialogFooter>
                 <DialogClose asChild>
                   <Button type="button" variant="outline">Cancel</Button>
                 </DialogClose>
                 <Button type="submit" disabled={isSubmitting}>
                   {isSubmitting ? 'Saving...' : 'Save Changes'}
                 </Button>
               </DialogFooter>
             </form>
           </Form>
         </DialogContent>
       </Dialog>

    </Card>
  );
}


    